.popup {
    z-index: 100;
    position: fixed;
    width: 470px;
    height: 330px;
    background-color: #320550;
    border-radius: 10px;
    top: calc(50vh - 165px);
    left: calc(50% - 235px);
    color: white;
}


.popup.hidden {
 display: none;
}

.modal-content {
    top: 0;
    left: calc(50vw - 320px);
}

.modal-header {
display: flex;
    align-items: center;
    justify-content: center;
    align-items: center;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #dee2e6;
    border-top-left-radius: calc(0.3rem - 1px);
    border-top-right-radius: calc(0.3rem - 1px);
    font-size: 20px;
    height: 50px;
}

.modal-title {
    align-self:center;
}