.adminHeader {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    row-gap:35px;
}

.headerContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #c291e2;;
    padding-left:20px;
    padding-right: 20px;
}

.nav-items {
    list-style-type: none;
    display: flex;
    column-gap: 40px;
}

.nav-items li a {
    text-decoration: none;
}

.nav-items li {
    font-size: 20px;
    text-transform: uppercase;
    font-weight: 800;
}

.logout-btn {
    border: none;
    background-color: transparent;
    font-family: inherit;
    padding: 0;
    cursor: pointer;
  
    @media screen and (-ms-high-contrast: active) {
      border: 2px solid currentcolor;
    }
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
}

.logout-btn a {
text-decoration: none;
}
 .headerContent  ul li a{
    text-decoration: none;
    color: #fff;
    padding: 10px 20px;
    font-weight: bold;
}

.headerContent ul li a:hover{
    background-color: white;
    color: #fff;
    color: #c291e2;
}

.headerContent .active{
    background-color: white;
    color: #c291e2;
}

.non-active {
    pointer-events: none;
}