.categoryCard {
    width: 340px;
height: 243px;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items: center;
color:#c291e2;
border: 2px solid #c291e2;
border-radius: 5px;
margin-bottom: 3%;
}

.addCategoryContent {
    font-size: 22px;
    line-height: 42px;
    display: flex;
    align-items: center;
    text-align: center;
    
    color: #310841;
    text-transform: capitalize;
        font-weight: 800;
        height: 30%;
}

.addCategory-btn {
    width: 85px;
    height: 85px;
    border: 3px solid #000000;
    box-sizing: border-box;
    border-radius: 90px;
    align-self: center;
    margin:auto;
    cursor: pointer;
}

.categoryHeader img {
    cursor: pointer;
}