.categoryCard {
    width: 340px;
height: 243px;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items: center;
color:#c291e2;
border: 2px solid #c291e2;
border-radius: 5px;
margin-bottom: 3%;
}

.categoryHeader {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

.category-count {
    font-weight: 900;
    text-transform: uppercase;
}

.categoryFooter {
    margin-bottom: 7%;
    display: flex;
    column-gap: 20%;
}

.update-btn, .add-btn {
    text-decoration: none;
    width: 143px;
height: 34.18px;
border: 2px solid #c291e2;
color:#c291e2;
font-weight: 800;
background-color: white;
box-sizing: border-box;
border-radius: 5px;
cursor: pointer;
}

.categoryHeader img {
    cursor: pointer;
}

.add-btn a {
    color:#c291e2;
text-decoration: none;
}
