.wordCard {
    width: 301px;
height: 408px;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items: center;
border: 2px solid #c291e2;
box-sizing: border-box;
border-radius: 5px;
}

.wordDesc{
    margin:3%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    row-gap:10px;
    height: 90%;
    color:#c291e2;
}

.wordContent {
    display: flex;
    height: 100%;
}

.change-btn {
    width: 251px;
height: 60px;
    border: 2px solid #c291e2;
    color:#c291e2;
    font-weight: 800;
    background-color: white;
    box-sizing: border-box;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 20px;
}

.card-img img {
    width: 70%;
}

.close-icon {
    margin:10px;
    width: 22px;
height: 22px;
cursor: pointer;
}

.card-img {
display: flex;
// row-gap: 10px;
flex-direction: column;
justify-content: center;
align-items: center;
}