.header-navbar {
    background-color: #c291e2;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items:center;
}

.menu-bars {
    cursor: pointer;
    margin-left:  2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    z-index: 6;
    background: #c291e2;
    width:  250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left:-100%;
    transition: 850ms;
    overflow: auto;
    flex-direction: column;
    overflow: auto;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.active-link {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
   background-color: pink;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width:  95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;

}

.nav-text a:hover {
    background: #7d19c0;
}

.nav-menu-items {
    overflow: auto;
    width: 100%;
}

.navbar-toggle {
    background: #c291e2;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}

.switcher {
    margin-right: 20%;
}

nav .btn-primary {
    margin:20%;
  background-color: #7d19c0;
  width: 70%;
  align-self: center;
} 

.popup {
    z-index: 100;
    position: fixed;
    width: 639px;
    height: 430px;
    background-color: #165488;
    border-radius: 10px;
    top: calc(50vh - 230px);
    left: calc(50% - 320px);
}

.btn-login {
    margin-top: 15%;
    margin-bottom: 15%;
    width:70%;
    align-self: center;
    background-color:#320550;
    display: inline-block;
    font-weight: 400;
    line-height: 1.5;
    color: #dbe0e6;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    cursor: pointer;
    -webkit-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}